body {
    font-family: 'Roboto' sans-serif;
    font-weight: 400;
    font-size: 1.6rem;
    color: $color-gray;
    letter-spacing: .2rem; }

.title-first {
    color: $color-white;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        margin-bottom: 1rem;
        animation-name: leftToright;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; } // определяет видно ли заднюю грань элнментп когда он повернут к пользователю
    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        animation-name: rightToleft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 6.5rem; } }
